'use strict';

document.addEventListener('DOMContentLoaded', () => {
  const phoneIcon = document.querySelector('.header_right img[alt="Phone"]');
  const contactsBlock = document.querySelector('.header_contacts');
  const burgerIcon = document.querySelector('img[alt="Menu"]');
  const menu = document.querySelector('.menu_nav');
  const closeBtn = document.querySelector('.menu_close');
  const menuLinks = document.querySelectorAll('.menu_content a');

  let hideTimeout;

  const showContacts = () => {
    clearTimeout(hideTimeout);
    contactsBlock.style.display = 'flex';
  };

  const hideContacts = () => {
    hideTimeout = setTimeout(() => {
      contactsBlock.style.display = 'none';
    }, 200); // задержка, чтобы успеть навестись на блок
  };

  phoneIcon.addEventListener('mouseenter', showContacts);
  phoneIcon.addEventListener('mouseleave', hideContacts);

  contactsBlock.addEventListener('mouseenter', showContacts);
  contactsBlock.addEventListener('mouseleave', hideContacts);

  burgerIcon.addEventListener('click', () => {
    menu.classList.remove('hidden');
  });

  closeBtn.addEventListener('click', () => {
    menu.classList.add('hidden');
  });

  menuLinks.forEach((link) => {
    link.addEventListener('click', () => {
      menu.classList.add('hidden');
    });
  });
});
document.querySelector('.header_text').addEventListener('click', (e) => {});
document.querySelectorAll('.menu_content a').forEach((link) => {
  link.addEventListener('click', function (event) {
    event.preventDefault();
    const targetId = this.getAttribute('href').substring(1);
    const targetElement = document.getElementById(targetId);
    if (targetElement) {
      targetElement.scrollIntoView({ behavior: 'smooth', block: 'start' });
    }
  });
});
